import React, {useEffect, useState} from 'react'
import './DayNightCycle.css'
import suncalc from 'suncalc'
import Moment from 'moment'
import { extendMoment } from 'moment-range';
const moment = extendMoment(Moment);

export default function DayNightCycle({latitude, longitude}) {
  const [sunPosition, setSunPosition] = useState(null);

  let colors = document.querySelectorAll('.bgColorInner');

  let endStartGrad = ['#743a6d', '#e63552', '#ffb668', '#fbcc0a', '#b1def9'];
  let goldenGrad = ['#ffffc2', '#ffffeb', '#dbf1f9', '#d4eef8', '#cdebf7'];
  let noonGrad = ['#b7e2f3', '#abddf1', '#9fd8ef', '#93d3ed', '#87ceeb'];
  let dawnDuskGrad = ['#432967', '#782d65', '#c3305d', '#f0445e', '#fd9367'];
  let setRiseGrad = ['#432967', '#782d65', '#c3305d', '#f0445e', '#fd9367']
  let nightGrad = [ '#002746', '#2b1f61','#013155', '#3b3380', '#003a63'];


  useEffect(() => {
    console.log(sunPosition)
    if (sunPosition === 'solarNoon') {
      colors.forEach((color, index) => {
        color.style.background = noonGrad[index]
      })
    } else if (sunPosition === 'goldenHour' || sunPosition === 'goldenHourEnd') {
      colors.forEach((color, index) => {
        color.style.background = goldenGrad[index]
      })
    } else if (sunPosition === 'sunrise' || sunPosition === 'sunset') {
      colors.forEach((color, index) => {
        color.style.background = setRiseGrad[index]
      })
    } else if (sunPosition === 'dawn' || sunPosition === 'dusk' || sunPosition === 'nauticalDawn' || sunPosition === 'nauticalDusk') {
      colors.forEach((color, index) => {
        color.style.background = dawnDuskGrad[index]
      })
    } else if (sunPosition === 'night' || sunPosition === 'nightEnd' || sunPosition === 'nadir') {
      colors.forEach((color, index) => {
        color.style.background = nightGrad[index]
      })
    } else if (sunPosition === 'sunsetStart' || sunPosition === 'sunriseEnd') {
      colors.forEach((color, index) => {
        color.style.background = endStartGrad[index]
      })
    }

    setTimeout(() => {
      colors.forEach(color => {
        color.classList.add('color');
      })

      setTimeout(() => {
        document.querySelector('.avatarInner').classList.add('opacity1');
      }, 750)
    }, 500)
  }, [sunPosition])


  useEffect(() => {
    InitSun();
  }, [latitude])


  const InitSun = () => {
      const d = new Date();
      let now = moment().set({
          'year': d.getFullYear(),
          'month': d.getMonth(),
          'date': d.getDate(),
          'hour': d.getHours(),
          'minute': d.getMinutes()
      })



    now = moment(now, 'ddd MMM DD YYYY HH:mm')
    console.log(now)

    const sunTimes = suncalc.getTimes(now, latitude, longitude)
    console.log(sunTimes)



    const times = (pos) => {
        return moment(sunTimes[pos], 'ddd MMM DD YYYY HH:mm')
    }

    const ranges = [
      moment.range(times('nadir'), times('nightEnd')), //nadir
      moment.range(times('nightEnd'), times('nauticalDawn')), //nightEnd
      moment.range(times('nauticalDawn'), times('dawn')), //nauticalDawn
      moment.range(times('dawn'), times('sunrise')), //dawn
      moment.range(times('sunrise'), times('sunriseEnd')), //sunrise
      moment.range(times('sunriseEnd'), times('goldenHourEnd')), //sunriseEnd
      moment.range(times('goldenHourEnd'), times('solarNoon')), //goldenHourEnd
      moment.range(times('solarNoon'), times('goldenHour')), //solarNoon
      moment.range(times('goldenHour'), times('sunsetStart')), //goldenHour
      moment.range(times('sunsetStart'), times('sunset')), //sunsetStart
      moment.range(times('sunset'), times('dusk')), //sunset
      moment.range(times('dusk'), times('nauticalDusk')), //dusk
      moment.range(times('nauticalDusk'), times('night')), //nauticalDusk
      moment.range(times('night'), times('nadir')), //night
        //remove nadir, unnecessary
    ];
    for (let i = 0; i < ranges.length; i++) {
      //CHANGE TO SWITCH CASE
      if (now.within(ranges[i])) {
          console.log(i)
          if (i == 0) {
          setSunPosition('nadir');
        }
          else if (i == 1) {
          setSunPosition('nightEnd');
          }
        else if (i == 2) {
          setSunPosition('nauticalDawn');
        }
      else if (i == 3) {
          setSunPosition('dawn');
        }
        else if (i == 4) {
          setSunPosition('sunrise');
        }
        else if (i == 5) {
          setSunPosition('sunriseEnd');
        }
        else if (i == 6) {
          setSunPosition('goldenHourEnd');
        }
          else if (i == 7) {
          setSunPosition('solarNoon');
        }
      else if (i == 8) {
          setSunPosition('goldenHour');
        }
      else if (i == 9) {
          setSunPosition('sunsetStart');
        }
        else if (i == 10) {
          setSunPosition('sunset');
        }
        else if (i == 11) {
          setSunPosition('nauticalDusk');
        }
        else if (i == 12) {
          setSunPosition('dusk');
        }
        else if (i == 13) {
          setSunPosition('night');
        }
      }
    }
  }

    return(
      <div className='sky'>
        <div id="sky" className={sunPosition}>
          <div className="skyInner Sun" />
          <div className='skyInner Moon'>
            <div className='moon-crater' />
            <div className='moon-crater' />
            <div className='moon-crater' />
          </div>
        </div>
      </div>
    )
}
